<template>
    <div class="cartcontrol">
        <transition name="move">
            <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
                <span class="icon-remove_circle_outline inner"></span>
            </div>
        </transition>

        <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
        <div class="icon-add_circle cart-add" @click.stop.prevent="addCart">

        </div>
    </div>
</template>
<script>
import Vue from "vue";
// import eventHub from "@/components/bus.js";
export default {
    props: {
        food: {
            type: Object
        }
    },
    methods: {
        addCart(event) {
            //判断是否有better-scroll派发的事件，如果是原生事件则返回，为了解决pc端和部分手机触发两次点击
            if (!event._constructed) {
                return;
            }
            if (!this.food.count) {
                Vue.set(this.food, 'count', 1);
            } else {
                this.food.count++;
            }
            //eventHub.$emit('cart-add', event.target);
            this.$bus.$emit('cart-add',event.target);
        },
        decreaseCart() {
            if (!event._constructed) {
                return;
            }
            if (this.food.count) {
                this.food.count--;
            }
        }

    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    .cartcontrol
        font-size:0
        .move-enter-active, .move-leave-active
            transition: all .4s linear
            opacity:1
            transform: translate3d(0,0,0)
            .inner
                transition:all 0.4s linear
                transform:rotate(0)
        .move-enter, .move-leave-active
            opacity: 0
            transform: translate3d(24px,0,0)
            .inner
                transform:rotate(180deg)
        .cart-decrease
            display:inline-block
            .inner
                display:inline-block
                padding:6px
                line-height:24px
                font-size:24px
                color:rgb(0,160,220)
        .cart-count
            display:inline-block
            vertical-align:top
            width:12px
            padding-top:6px
            line-height:24px
            text-align:center
            font-size:10px
            color:rgb(147,153,159)
        .cart-add
            display:inline-block
            padding:6px
            line-height:24px
            font-size:24px
            color:rgb(0,160,220)
</style>


